@charset "utf-8";
.product{  width: 1200px;
  margin: 0 auto;}
.product h3{text-align: center;}
.product_list{
  display: flex;
  justify-content: space-around;
  padding: 0px;
}
.product_list > li{
  width: 23%;
  list-style:none;
  overflow: hidden;
  text-align: right;
  font-weight: bold;
}

.product_title{
  padding-bottom: 20px ;
}
.price{
  color: #f00;
  display: block;
  text-align: right;
}